import { light, dark, } from "../assets/style/theme"
const changeTheme = ({ type }) => {
    console.log('to' + type)
    if (type === 'dark') {
        Object.keys(dark).forEach((property, index) => {
            document.documentElement.style.setProperty(`--${property}`, dark[property]);
        })
    } else {
        Object.keys(light).forEach((property, index) => {
            document.documentElement.style.setProperty(`--${property}`, light[property]);
        })
    }
}
const isDark = () => {
    return window.matchMedia('(prefers-color-scheme: dark)').matches
}
const listenDrakMode = () => {
    const dark = 'dark'
    const light = 'light'

    if (isDark()) {
        console.log('首次为暗色')
        changeTheme({ type: dark })
    } else {
        changeTheme({ type: light })
    }
    window.matchMedia('(prefers-color-scheme: dark)').addListener(({ matches }) => {
        matches ? changeTheme({ type: 'dark' }) : ''
    })
    window.matchMedia('(prefers-color-scheme: light)').addListener(({ matches }) => {
        matches ? changeTheme({ type: 'light' }) : ''
    })
}
const unListenDrakMode = () => {
    window.matchMedia('(prefers-color-scheme: dark)').addListener(() => { })
    window.matchMedia('(prefers-color-scheme: light)').addListener(() => { })
}
export {
    changeTheme,
    listenDrakMode,
    unListenDrakMode
}